.game-header {
  background: #333;
  padding: 30px 50px 60px;
  position: relative;
  z-index: 1;
  overflow: hidden;
  text-align: center;
  color: #FFF;

  a {
    color: #FFF;
    border-bottom: 0;

    &:hover {
      color: rgba(#FFF,.6);
    }
  }

  &:after {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: rgba(#000,.5);
    z-index: -1;
  }
  &-bg {
    position: absolute;
    z-index: -2;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    min-width: 100%;
    min-height: 100%;
    object-fit: cover;
    filter: blur(2px);
  }
  &-number {
    margin-bottom: 6px;
  }
  &-place {
    margin-bottom: 10px;

    &:before {
      content: "";
      display: inline-block;
      vertical-align: middle;
      margin-right: 7px;
      position: relative;
      top: -1px;
      width: 9px;
      height: 14px;
      background: url(../images/matches/pin-white.svg) center no-repeat;
      background-size: contain;
    }
    a {
      border-bottom: 1px dashed #FFF;

      &:hover {
        border-bottom-color: transparent;
      }
    }
  }
  &-dash {
    margin-top: 30px;
    margin-bottom: 45px;
    position: relative;
    height: 1px;

    &:before {
      content: "";
      position: absolute;
      left: -50px;
      right: -50px;
      background: rgba(#FFF,.2);
      height: 1px;
    }
  }
  &-team {
    flex: 0 0 35%;
    max-width: 35%;
    padding: 0px 20px;
  }
  &-info {
    flex: 0 0 30%;
    max-width: 30%;
  }
  &-team {
    &-logo {
      height: 60px;
      margin-bottom: 15px;
      display: -ms-flex;
      display: flex;
      -ms-align-items: center;
      align-items: center;
      justify-content: center;

      img {
        max-height: 60px;
      }
    }
    &-name {
      display: block;
      @include fz(28);
      font-weight: 500;
      margin-bottom: 3px;
    }
    &-location {
      margin-bottom: 8px;
    }
    &-rates {
      display: inline-block;
      position: relative;
      padding-left: 20px;
      margin-bottom: 8px;

      &:before {
        content: "";
        position: absolute;
        left: 0;
        top: 50%;
        margin-top: -6px;
        width: 13px;
        height: 10px;
        background: url(../images/matches/team-rates.svg) center no-repeat;
        background-size: contain;
      }
    }
    &-games {
      font-size: 0;
    }
  }
  &-game-link {
    display: inline-block;
    vertical-align: middle;
    margin-right: 3px;
    margin-left: 3px;
    margin-bottom: 4px;
    width: 12px;
    height: 12px;
    border-radius: 50%;
    background: $primary;

    &.red {
      background: $red;
    }
    &:hover {
      box-shadow: 0px 0px 0px 1px #FFF, inset 0px 0px 0px 1px #FFF;
    }
  }
  &-info {
    &-time {
      font-weight: 700;
      @include fz(25);
      margin-bottom: 0px;
    }
    &-date {
      @include fz(22);
      margin-bottom: 15px;
    }
    &-quotes {
      background: #FFF;
      padding: 8px 10px;
      display: flex;
      align-items: center;
      justify-content: space-between;
      color: $font;
      width: 170px;
      max-width: 100%;
      margin: 0px auto 15px;
    }
    &-quote {
      @include fz(16);
      padding: 0px 5px;
    }
    &-ticket {
      @include fz(16);
      position: relative;
      padding-left: 24px;

      &:before {
        content: "";
        position: absolute;
        left: 0;
        top: 0;
        width: 18px;
        height: 18px;
        background: url(../images/matches/ticket.svg) center no-repeat;
        background-size: contain;
      }
      span {
        border-bottom: 1px dashed #FFF;
      }
      &:hover {
        span {
          border-color: transparent;
        }
      }
    }
  }
}

.game-tabs {
