}
.header-tabs {
  border-bottom: 2px solid $border;
  margin-bottom: 35px;

  &-title {
    @include fz(28);
    margin-right: 5vw;
    margin-top: 0;
    line-height: 1.1;
    margin-bottom: 25px;
  }
  ul#{&}-tabs {
    margin: 0;
    padding: 0;
    @include fz(16);

    li {
      display: inline-block;
      position: static;
      padding: 0;
      margin: 0px 5vw 0 0;

      &:before {
        content: none !important;
      }
    }
    a {
      display: block;
      position: relative;
      padding-bottom: 25px;
      border: 0;
      color: $light;

      &:after {
        content: "";
        position: absolute;
        left: 0;
        right: 0;
        bottom: -2px;
        background: $red;
        height: 2px;
        z-index: 1;
        opacity: 0;
        transition: opacity .3s;
      }
      &:hover, &.selected {
        &:after {
          opacity: 1;
        }
      }
      &.selected {
        color: $font;
      }
    }
  }
}
